// CampaignApp.jsx
import { useState } from "react";
import { ethers } from "ethers";
import { abi } from "./utils/infoContract";
const CONTRACT_ADDRESS = "0xac0974bec39a17e36ba4a6b4d238ff944bacb478cbed5efcae784d7bf4f2ff80"; // ton adresse de contrat déployé

export default function CampaignApp() {
  const [goal, setGoal] = useState("");
  const [duration, setDuration] = useState("");
  const [txHash, setTxHash] = useState("");

  async function createCampaign() {
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      await provider.send("eth_requestAccounts", []);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(CONTRACT_ADDRESS, abi, signer);

      const tx = await contract.createCampaign(goal, Number(duration));
      const receipt = await tx.wait();

      setTxHash(receipt.transactionHash);
      console.log("Campaign created with tx:", receipt.transactionHash);
    } catch (error) {
      console.error("Error creating campaign:", error);
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-900 text-white p-4">
      <div className="w-full max-w-md bg-gray-800 p-6 rounded-2xl shadow-lg space-y-4">
        <h1 className="text-2xl font-bold text-center">Create Campaign</h1>
        
        <input
          type="text"
          placeholder="Campaign goal"
          value={goal}
          onChange={(e) => setGoal(e.target.value)}
          className="w-full p-2 rounded bg-gray-700 focus:outline-none"
        />
        <input
          type="number"
          placeholder="Duration (in seconds)"
          value={duration}
          onChange={(e) => setDuration(e.target.value)}
          className="w-full p-2 rounded bg-gray-700 focus:outline-none"
        />

        <button
          onClick={createCampaign}
          className="w-full bg-blue-600 hover:bg-blue-700 text-white p-2 rounded-xl"
        >
          Create
        </button>

        {txHash && (
          <p className="text-green-400 break-all text-sm">
            ✅ Campaign created!<br />Tx: {txHash}
          </p>
        )}
      </div>
    </div>
  );
}
